import PropTypes from "prop-types";
import { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useSnackbar } from "notistack";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";
import {
  alpha,
  experimentalStyled as styled,
  useTheme,
} from "@material-ui/core/styles";
import { Stack, FormHelperText } from "@material-ui/core";
import fakeRequest from "../../../utils/fakeRequest";
import { useDispatch, useSelector } from "../../../redux/store";
import { updatePost, getPostSuccess } from "../../../redux/slices/fundraise";
import { QuillEditor } from "../../editor";

// ----------------------------------------------------------------------

FundraiseEditStory.propTypes = {
  renderForm: PropTypes.func,
  post: PropTypes.object,
};

export default function FundraiseEditStory({ renderForm, post }) {
  const theme = useTheme();
  const params = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const isLight = theme.palette.mode === "light";

  const NewBlogSchema = Yup.object().shape({
    content: Yup.string().min(100).required("This is required"),
  });

  const formik = useFormik({
    initialValues: {
      uid: post.uid,
      description: post.description,
      content: post.description.content,
    },
    validationSchema: NewBlogSchema,
    onSubmit: async (values, { setSubmitting, resetForm }) => {
      try {
        await fakeRequest(500);
        resetForm();
        setSubmitting(false);
        dispatch(getPostSuccess(null));
        dispatch(
          updatePost({
            uid: values.uid,
            description: values.description,
          })
        );
        enqueueSnackbar("Save success", { variant: "success" });
        // navigate(-1);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
      }
    },
  });

  const {
    errors,
    values,
    touched,
    handleSubmit,
    isSubmitting,
    setFieldValue,
    getFieldProps,
  } = formik;

  useEffect(() => {
    renderForm(formik);
  }, [values]);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Stack spacing={theme.shape.CARD_CONTENT_SPACING}>
          <QuillEditor
            id="product-description"
            simple
            value={values.description.content}
            onChange={(content, delta, source, editor) => {
              const text = editor.getText(content);

              setFieldValue("content", content);
              setFieldValue("description", { content, text });
            }}
            error={Boolean(touched.content && errors.content)}
          />
          {touched.content && errors.content && (
            <FormHelperText error sx={{ px: 2 }}>
              {touched.content && errors.content}
            </FormHelperText>
          )}
        </Stack>
      </Form>
    </FormikProvider>
  );
}
